import {Constants} from '../core/constants';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserSession } from '../core/sessions';

@Component({
  selector: 'app-share-component',
  templateUrl: './share-component.component.html',
  styleUrls: ['./share-component.component.css']
})
export class ShareComponentComponent implements OnInit {

  constructor(private _activatedRoute: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this._activatedRoute.params.subscribe(params => {
      const userAgent = UserSession.getMobileOperatingSystem();
      const type = params['type'];
      const id = params['id'];
      const name = params['name'];
      if (type && id && name) {
        if (userAgent === 'web') {
          this.router.navigate([type, id, name]);
        }
       else if (userAgent === 'Android') {
          this.router.navigate([type, id, name]);

        }
       else if (userAgent === 'iOS') {
     this.router.navigate([type, id, name]);
 
        }
        else{
          this.router.navigate([type, id, name]);
 
        }
      } else {
        this.router.navigate(['/']);
      }
    });
  }






}



